import { Suspense, lazy } from "react";
import { useLocation, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../../Loader/QvipleLoader";
const LmsReviewAuthorityStaff = lazy(() => import("./LmsReviewAuthorityStaff"));
const LmsReviewAuthorityRequest = lazy(() =>
  import("./LmsReviewAuthorityRequest")
);
const LmsReviewAuthorityHistory = lazy(() =>
  import("./LmsReviewAuthorityHistory")
);

const LmsReviewAuthority = () => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  return (
    <>
      <Suspense fallback={<QvipleLoader />}>
        {searchParams.get("type") === "mod-staff" ? (
          <LmsReviewAuthorityStaff
            modId={getQuery?.state?.onClickValue?._id}
            instituteId={getQuery?.state?.onClickValue?.institute?._id}
          />
        ) : searchParams.get("type") === "mod-request" ? (
          <LmsReviewAuthorityRequest
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : searchParams.get("type") === "mod-history" ? (
          <LmsReviewAuthorityHistory
            modId={getQuery?.state?.onClickValue?._id}
          />
        ) : null}
      </Suspense>
    </>
  );
};

export default LmsReviewAuthority;
